body,
html {
  height: 100%;
  margin: 0;
  background-color: gray;
  font-family: Arial, Helvetica, sans-serif;
}

.Navigator {
  margin: 0 auto;
  font-size: 25px;
  height: 100px;
  width: 100px;
  text-align: center;
  padding-top: 10px;
}

.skills {
  width: 400px;
  height: 200px;
  border: 1px;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
}

h1 {
  text-align: center;
}

.fontColour {
  color: beige;
}
